@media (max-width: 414px) {
    .nav {
        border: none;
    }

    .nav li.nav-item {
        margin-bottom: 0;
    }

    .nav li.nav-item:last-child {
        border: none;
    }

    .nav li.nav-item a.nav-link {
        border: none;
    }

    .navigation header.navigation-header .nav {
        display: flex;
    }

    body.dark .nav li.nav-item {
