    }
}

@media print {
    .page-header {
        display: none;
    }
}

@media (max-width: 1200px) {
    body.dark .header .header-left {
        background: none !important;
    }

    body.dark .header .header-body {
        background-color: #383a3f;
    }

    body.dark .header .header-body ul li a {
